

import tools from '../../lib/tools.js';
import Toast from 'vux/src/components/toast/index.vue'

export default {
name:'messageIndex',
components:{Toast},
data(){
return {
showToast:false,
toastMsg:'',
id:'',
doctorId:'',
newsList:[]
}
},
mounted(){
let id = (localStorage.getItem('md') || tools.getCookie('user_id'));
if((!id) || id=='undefined'){
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
return false;
}// 此时可以跳转到登陆，等登陆后在跳转回来
this.doctorId = tools.getRequestParam('id');
this.id = id;
this.updateState();
},
methods:{
init(){
let vm = this;
this.$http.axios({method:'POST',url:'/message/getPageMessages',data:{pageSize:1,pageNumber:100,messageTo:this.id,type:3,messageFrom:this.doctorId}}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
vm.newsList = data.items;
}else{
console.log(data.resultCode);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '操作失败';
})
},
updateState(){
let vm = this;
this.$http.post('/message/readOverMessage',{userId:this.id,messageFromId:this.doctorId,type:3}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
vm.init();


}else{
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
}
}
}



